
@charset "UTF-8";
/** stylesheets
    - author Barry
 */
.topBorder, .mine-money {
  position: relative;
}
.topBorder:before, .mine-money:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.bottomBorder, .user-list li {
  position: relative;
}
.bottomBorder:after, .user-list li:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.doubleBorder, .apply-search {
  position: relative;
}
.doubleBorder:after, .apply-search:after, .doubleBorder:before, .apply-search:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.doubleBorder:after, .apply-search:after {
    bottom: 0;
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
.doubleBorder:before, .apply-search:before {
    top: 0;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
.thumbBorder, .apply-search .form {
  position: relative;
}
.thumbBorder:after, .apply-search .form:after {
    position: absolute;
    top: -0.5px;
    left: -0.5px;
    width: 200%;
    height: 200%;
    content: '';
    border: 1px solid #ccc;
    border-radius: 3px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform-origin: top left;
            transform-origin: top left;
}
.clearfix:after, .clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}

/* ==========================================================================
 有即是无，无即是有
 ========================================================================== */
body, dl, dd,
ul, ol,
h1, h2, h3, h4, h5, h6, pre,
form, fieldset, legend, input, textarea, optgroup,
p, blockquote, figure, hr, menu, dir,
thead, tbody, tfoot, th, td {
  margin: 0;
  padding: 0;
}

/**
* 非大面积文字排版网站通常不需要列表项，如果需要可单独设置
*/
ul, ol {
  list-style: none;
}

/* ==========================================================================
 链接
 ========================================================================== */
/**
* 1. 去除链接默认的下划线，提高文字可读性
*/
a {
  text-decoration: none;
  /* 1 */
  color: #333;
}

/**
* 去掉点击时的焦点框，同时保证使用键盘可以显示焦点框
*/
a:active,
a:hover {
  outline-width: 0;
}

/* ==========================================================================
 字体和基础排版
 ========================================================================== */
/**
* 1.防止 iOS 横屏字号放大，同时保证在PC上 zoom 功能正常
*/
html {
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  /* 1 */
  font-size: 62.5%;
  /* 10/16=62.5% */
}
body {
  background-color: #fff;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}
body,
button, input, select, textarea {
  line-height: 1.6;
  font-size: 12px;
}

/* ==========================================================================
 嵌入元素
 ========================================================================== */
img {
  max-width: 100%;
  vertical-align: top;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

/**
* 重置按钮禁用时光标样式
*/
button[disabled],
input[disabled] {
  cursor: default;
}

/**
* 1.移除 OS X 中 Safari5 和 Chrome 搜索框内侧的左边距
* 2.如果需要隐藏清除按钮需要加上
 input[type="search"]::-webkit-search-cancel-button
*/
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  resize: vertical;
  /* 2 */
}

/**
* label 元素给予手型，暗示此处可点击
*/
label {
  cursor: pointer;
}
i, em {
  font-style: normal;
}
input, button, textarea, select {
  outline: none;
  border: none;
}
input[type="text"] ，input[type="number"], textarea {
  -webkit-appearance: none;
}
