html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
::-moz-selection {
  background: #fec963;
  color: white;
}
::selection {
  background: #fec963;
  color: white;
}
::-webkit-scrollbar {
  width: 0;
}
.page-con {
  padding: 15px;
  max-width: 540px;
  margin: 0 auto;
  min-width: 320px;
}
.btn {
  height: 35px;
  background-color: #fec963;
  color: #333;
  border-radius: 3px;
  display: block;
  width: 100%;
}
.btn-air {
  height: 35px;
  background-color: transparent;
  border: 1px solid #fec963;
  color: #fec963;
  border-radius: 3px;
}
.login-bg {
  height: 100vh;
  background: url("./assets/img/login-bg.png") center/cover no-repeat;
}
.login-con {
  padding: 85px 35px 0;
}
.login-form {
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  padding: 20px 15px;
}
.login-form .logo {
    display: block;
    height: 75px;
    width: 75px;
    margin: -58px auto 0;
}
.form-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 35px;
  margin-bottom: 15px;
  font-size: 15px;
}
.form-group label {
    width: 75px;
    text-align: right;
}
.form-group input {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 0 5px;
}
.form-group textarea {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 5px;
    height: 120px;
}
.form-group .code {
    width: 75px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.form-group select {
    border: 1px solid #ccc;
    border-radius: 3px;
    width: 70px;
    display: block;
    background-color: #ffffff;
}
.form-group .vux-datetime {
    display: block;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 0;
    height: 35px;
}
.form-group .vux-datetime input {
      display: block;
      width: 100%;
      height: 100%;
}
.login-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 25px;
  margin-top: 20px;
  border-bottom: 1px solid #999;
  padding: 1px 0;
}
.login-group input {
    color: #ffffff;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    background-color: transparent;
    width: 1%;
}
.login-group input::-webkit-input-placeholder {
      color: #eee;
}
.login-group input:-ms-input-placeholder {
      color: #eee;
}
.login-group input::placeholder {
      color: #eee;
}
.login-group label {
    width: 20px;
}
.login-group img {
    vertical-align: middle;
}
.txt-box h2 {
  font-size: 15px;
  line-height: 2.5;
  text-align: center;
}
.txt-box p {
  color: #666666;
  line-height: 1.8;
}
.txt-con2 p {
  text-indent: 2em;
}
.price-list {
  font-size: 15px;
  overflow: hidden;
}
.price-list dt {
    line-height: 1;
    margin-bottom: 15px;
}
.price-list dd {
    float: left;
    border: 1px solid #ccc;
    border-radius: 3px;
    text-align: center;
    line-height: 34px;
    width: 31%;
    margin-right: 3.5%;
    margin-bottom: 10px;
}
.price-list dd:nth-child(3n+1) {
      margin-right: 0;
}
.apply-search {
  border-bottom: 10px solid #eee;
  padding: .3rem;
}
.apply-search .form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 35px;
}
.apply-search input {
    display: block;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 10px;
    position: relative;
    z-index: 2;
    background: transparent;
}
.apply-search button {
    background: url("./assets/img/icon-search.png") center/18px no-repeat #fec963;
    display: block;
    width: 40px;
    height: 34px;
    position: relative;
    z-index: 2;
}
.mine-money {
  padding: 15px;
}
.mine-money p {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    line-height: 25px;
}
.mine-money a {
    display: block;
    width: 65px;
    text-align: center;
    height: 25px;
    line-height: 23px;
    font-size: 13px;
}
.mine-money li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 15px;
}
.user-list {
  padding: 0;
}
.user-list .apply-search:before {
    display: none;
}
.user-list ul {
    font-size: 15px;
}
.user-list li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    line-height: 50px;
    padding: 0 15px;
}
.user-list li p {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      width: 1%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.user-list li p:nth-child(2) {
        text-align: center;
}
.user-list li p:nth-child(3) {
        text-align: right;
}
.user-list li:first-child {
      color: #999;
}
.img-warp {
  position: relative;
  padding-top: 100%;
}
.img-warp img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.btn-block {
  height: 35px;
  background-color: #fec963;
  color: #333;
  border-radius: 3px;
  display: block;
  text-align: center;
  line-height: 35px;
  margin: 15px;
}
.apply-form .weui-cell {
  padding: 0;
}



