
.topBorder {
  position: relative;
}
.topBorder:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.bottomBorder, .money-header {
  position: relative;
}
.bottomBorder:after, .money-header:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.doubleBorder {
  position: relative;
}
.doubleBorder:after, .doubleBorder:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.doubleBorder:after {
    bottom: 0;
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
.doubleBorder:before {
    top: 0;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
.thumbBorder {
  position: relative;
}
.thumbBorder:after {
    position: absolute;
    top: -0.5px;
    left: -0.5px;
    width: 200%;
    height: 200%;
    content: '';
    border: 1px solid #ccc;
    border-radius: 3px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform-origin: top left;
            transform-origin: top left;
}
.clearfix:after, .clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.money-header {
  font-size: 13px;
  line-height: 1.8;
  color: #999999;
  padding-bottom: 10px;
}
.money-detail P {
  color: #ff6060;
}
.money-detail h2 {
  font-size: 15px;
  line-height: 50px;
}
.money-detail input {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 0 5px;
  display: block;
  height: 35px;
  width: 100%;
  margin-bottom: 15px;
}



